import React, { Component } from 'react'
import EmployeeService from '../services/EmployeeService'

class ViewEmployeeComponent extends Component {
    constructor(props) {
        super(props)

        this.state = {
            id: this.props.match.params.id,
            employee: {}
        }
    }

    componentDidMount(){
        EmployeeService.getEmployeeById(this.state.id).then( res => {
            this.setState({employee: res.data});
        })
    }
    cancel() {
        this.props.history.push('/employees');
    }
    render() {
        return (
            <div>
                <br></br>
                <div className = "card col-md-6 offset-md-3">
                    <h3 className = "text-center"> View Employee Details</h3>
                    <div className = "card-body">
                        <div className = "row">
                            <label> Employee Code: { this.state.employee.code }</label>
                        </div>
                        <div className = "row">
                            <label> Employee Name: {this.state.employee.name}</label>
                        </div>
                        <div className = "row">
                            <label> Employee Email: {this.state.employee.email}</label>
                        </div>
                        <div className = "row">
                            <label> Employee Age: {this.state.employee.age}</label>
                        </div>
                        <div className = "row">
                            <label> Employee Phone: {this.state.employee.phone}</label>
                        </div>

                        <button className="btn btn-danger" onClick={this.cancel.bind(this)} style={{marginLeft: "10px"}}>Cancel</button>
                    </div>

                </div>
            </div>
        )
    }
}

export default ViewEmployeeComponent